.main-btn{
	background-color: white;
    text-align: center;
    margin:12px;
	font-family: 'Raleway', sans-serif;
	color: hsl(220, 80%, 50%);
	padding: 10px;
	width: 150px;

	box-shadow: 
		0px 0px 0px 10px hsl(330, 80%, 50%),
		0px 10px 0px 10px hsl(330, 80%, 40%),
		0px 20px 20px 15px #0003;
	border-radius: 50px 15px 50px 15px;
	
}

.main-btn:active {
	transform: translateY(4px);
  }

.main-btn:focus{
	outline: none;
	border: none;
}

